body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  background-color: rgb(160, 160, 160);
}

.App {
  margin: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: white;
  border: 1px solid black;
  border-radius: 10px;

  &__title {
    text-align: center;
  }
}

.Goods {
  &__list {
    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 0;
    list-style: none;
  }

  &__item {
    padding: 10px;
  }

  &__buttonList {
    display: flex;
    justify-content: center;
  }

  &__button {
    max-width: max-content;
    padding: 5px;
    margin: 5px;

    border: 1px solid black;
    border-radius: 5px;
    background-color: white;
    transition: all, 0.3s;

    &:hover {
      cursor: pointer;
      transform: translateY(-4px);
      box-shadow: 4px 4px rgb(160, 160, 160);
    }
  }
}
